.container-card{
    margin: 10px;
    padding: 0;
    width: 200px;
    height: 300px;
    background-color: #17a2b8;
    display: flex;
    flex-direction: column;
    align-content: center;
    border-radius: 5%;
}

.sub-container{
    flex: 1;
    margin: 10px;
    position: relative;
}

.layer-opacity{
    z-index: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0.6;
    background-color: #707070;
    border-radius: 5%;
}

.info-card{
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 2;
    padding: 5px;
    display: flex;
    flex-direction: column-reverse;
}

.row-ic{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.spacer{
    flex: 1;
}

.ic-edit{
    width: 40px;
    height: 40px;
    margin-bottom: 3px;
    margin-right: 3px;
}

.txt{
    color: #fff;
}
